.wl-modal {
  display: none;
  z-index: 99999 !important;
  height: 65%;
  width: 55%;
  background-color: white;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 10px !important;
  padding: 10px; }
  .wl-modal.wl-modal--open {
    display: block; }
  .wl-modal.wl-modal--closed {
    display: none; }
