.wl-action-button {
  border-radius: 8px !important;
  width: 100%;
  margin: 3px;
  padding: 10px;
  font-size: 13px;
  text-align: center;
  text-transform: capitalize;
  cursor: pointer;
  border: 1px solid #eee; }
  .wl-action-button.wl-action-button--primary {
    background-color: #177efb !important;
    color: #fff !important;
    font-weight: bold; }
  .wl-action-button.wl-action-button--normal {
    background-color: #c2e5ff !important;
    color: #177efb !important;
    font-weight: bold; }
